import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { FiPower, FiTrash2 } from "react-icons/fi";

//Conecção com o Banco
import api from "../../services/api";

import "./styles.css";
import logo from "../../assets/logo.svg";

export default function Profile() {
  const [incident, setIncident] = useState([]);
  const ongName = localStorage.getItem("ongName");
  const ongId = localStorage.getItem("ongId");

  const history = useHistory();


  useEffect(() => {
    api
      .get("profile", {
        headers: {
          Authorization: ongId,
        },
      })
      .then((response) => {
        setIncident(response.data);
      });
  }, [ongId]);

  async function deletarCaso(id) {
    try {
    const response = await api.delete(`incident/delete/${id}`, {
      headers: {
        Authorization: ongId,
      },
    });
    setIncident(incident.filter(incident => incident.id !== id))
    console.log(response);
    } catch (error) {
      console.log("ERROOO");
      
    }
    
  }

  function logout(){    
    localStorage.clear();
    history.push('/')
    
  }

  return (
    <div className="profile-container">
      <header>
        <img src={logo} alt="Be The Hero" />
        <span>Bem Vinda , {ongName}</span>
        <Link  className="button" to="/incident/novo">
          Cadastrar Novo Caso
        </Link>
        <button onClick={logout} type="button">
          <FiPower size={18} color="#E02041" />
        </button>
      </header>

      <h1>Casos Cadastrados</h1>
      <ul>
        {incident.map((incident) => (
          <li key={incident.id}>
            <strong>CASO:</strong>
            <p>{incident.title}</p>

            <strong>DESCRIÇÃO:</strong>
            <p>{incident.description}</p>

            <strong>VALOR:</strong>
            <p>{incident.value}</p>

            <button onClick={() => deletarCaso(incident.id) } type="button">
              <FiTrash2 size={20} color="#a8a8b3" />
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
}
